<template><section>
<h2 id="dialog-dui-hua-kuang"><a class="header-anchor" href="#dialog-dui-hua-kuang" aria-hidden="true">¶</a> Dialog 对话框</h2>
<p>在保留当前页面状态的情况下，告知用户并承载相关操作。</p>
<h3 id="ji-ben-yong-fa"><a class="header-anchor" href="#ji-ben-yong-fa" aria-hidden="true">¶</a> 基本用法</h3>
<p>Dialog 弹出一个对话框，适合需要定制性更大的场景。</p>
<docs-demo-block> 
                                                <div class="source" slot="source"><fm-button @click="showDialog(1)">显示Dialog</fm-button>
<fm-button @click="showDialog(2)">警告Dialog</fm-button>

   
</div>
                                                <p>Dialog 以 <code>$fmdialog</code> 属性挂载在 Vue Components 上, 可以在组件内通过 <code>this.$fmdialog</code> 的方式直接调用。</p>
<pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">fm-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"showDialog(1)"</span>&gt;</span>显示Dialog<span class="hljs-tag">&lt;/<span class="hljs-name">fm-button</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">fm-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"showDialog(2)"</span>&gt;</span>警告Dialog<span class="hljs-tag">&lt;/<span class="hljs-name">fm-button</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">methods</span>: {
        showDialog(flag) {
            <span class="hljs-keyword">this</span>.$fmdialog({
                <span class="hljs-attr">type</span>: flag === <span class="hljs-number">1</span> ? <span class="hljs-string">'success'</span> : <span class="hljs-string">'failure'</span>,
                <span class="hljs-attr">message</span>: <span class="hljs-string">'我是对话框'</span>
            });
        }
    }
  };
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>   
</code></pre>
</docs-demo-block>
<p>对话框弹出默认是没有后层遮罩的, 如果需要遮罩, 可以设置 <code v-pre="">mask</code> 属性.</p>
<docs-demo-block> 
                                                <div class="source" slot="source"><fm-button @click="showMaskDialog">显示Mask</fm-button>

   
</div>
                                                <p><code>mask</code> 属性是一个 <code>Boolean</code> 值, 默认是 <code>false</code>.</p>
<pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">fm-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"showMaskDialog"</span>&gt;</span>显示Mask<span class="hljs-tag">&lt;/<span class="hljs-name">fm-button</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">methods</span>: {
        showMaskDialog() {
            <span class="hljs-keyword">this</span>.$fmdialog({
                <span class="hljs-attr">type</span>: <span class="hljs-string">'success'</span>,
                <span class="hljs-attr">message</span>: <span class="hljs-string">'我是对话框, 并且带了炫酷的遮罩'</span>,
                <span class="hljs-attr">mask</span>: <span class="hljs-literal">true</span>
            });
        }
    }
  };
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>   
</code></pre>
</docs-demo-block>
<p>对于对话框需要和用户进行交互的, 不需要指定 <code v-pre="">type</code> 值.</p>
<docs-demo-block> 
                                                <div class="source" slot="source"><fm-button @click="showInteractionDialog(false)">两个按钮</fm-button>
<fm-button @click="showInteractionDialog(true)">一个按钮</fm-button>

   
</div>
                                                <p><code>isSingle</code> 属性是一个 <code>Boolean</code> 值, 默认是 <code>false</code>, 表示是否只显示确认按钮. 显示带交互行为的对话框时, 需要传入 <code>onConfirm</code> 回调.</p>
<pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">fm-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"showInteractionDialog(false)"</span>&gt;</span>两个按钮<span class="hljs-tag">&lt;/<span class="hljs-name">fm-button</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">fm-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"showInteractionDialog(true)"</span>&gt;</span>一个按钮<span class="hljs-tag">&lt;/<span class="hljs-name">fm-button</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">methods</span>: {
        showInteractionDialog(isSingle) {
            <span class="hljs-keyword">this</span>.$fmdialog({
                <span class="hljs-attr">message</span>: <span class="hljs-string">'有交互行为的对话框'</span>,
                isSingle,
                <span class="hljs-attr">onConfirm</span>: <span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> alert(<span class="hljs-string">'点击了确认'</span>)
            });
        }
    }
  };
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>   
</code></pre>
</docs-demo-block>
<h3 id="shu-xing"><a class="header-anchor" href="#shu-xing" aria-hidden="true">¶</a> 属性</h3>
<table class="table"><thead>
<tr>
<th style="text-align:left">参数</th>
<th style="text-align:left">说明</th>
<th style="text-align:left">类型</th>
<th style="text-align:left">可选值</th>
<th style="text-align:left">默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">mask</td>
<td style="text-align:left">是否显示遮罩</td>
<td style="text-align:left">boolean</td>
<td style="text-align:left">-</td>
<td style="text-align:left">false</td>
</tr>
<tr>
<td style="text-align:left">classes</td>
<td style="text-align:left">用于 Dialog 的类名</td>
<td style="text-align:left">string</td>
<td style="text-align:left">-</td>
<td style="text-align:left">''</td>
</tr>
<tr>
<td style="text-align:left">title</td>
<td style="text-align:left">Dialog 的标题</td>
<td style="text-align:left">string</td>
<td style="text-align:left">—</td>
<td style="text-align:left">'请确认'</td>
</tr>
<tr>
<td style="text-align:left">type</td>
<td style="text-align:left">类型</td>
<td style="text-align:left">string</td>
<td style="text-align:left">success/failure</td>
<td style="text-align:left">''</td>
</tr>
<tr>
<td style="text-align:left">duration</td>
<td style="text-align:left">自动关闭的时间</td>
<td style="text-align:left">number</td>
<td style="text-align:left">-</td>
<td style="text-align:left">1500</td>
</tr>
<tr>
<td style="text-align:left">message</td>
<td style="text-align:left">消息体</td>
<td style="text-align:left">string</td>
<td style="text-align:left">-</td>
<td style="text-align:left">''</td>
</tr>
<tr>
<td style="text-align:left">cancelBtnText</td>
<td style="text-align:left">取消按钮的文本</td>
<td style="text-align:left">string</td>
<td style="text-align:left">-</td>
<td style="text-align:left">'取消'</td>
</tr>
<tr>
<td style="text-align:left">confirmBtnText</td>
<td style="text-align:left">确认按钮的文本</td>
<td style="text-align:left">string</td>
<td style="text-align:left">-</td>
<td style="text-align:left">'确认'</td>
</tr>
<tr>
<td style="text-align:left">isSingle</td>
<td style="text-align:left">是否显示取消按钮</td>
<td style="text-align:left">boolean</td>
<td style="text-align:left">-</td>
<td style="text-align:left">false</td>
</tr>
<tr>
<td style="text-align:left">onCancel</td>
<td style="text-align:left">点击取消按钮时的回调</td>
<td style="text-align:left">function</td>
<td style="text-align:left">-</td>
<td style="text-align:left">-</td>
</tr>
<tr>
<td style="text-align:left">onConfirm</td>
<td style="text-align:left">点击确认按钮时的回调</td>
<td style="text-align:left">function</td>
<td style="text-align:left">-</td>
<td style="text-align:left">-</td>
</tr>
</tbody>
</table>
</section></template>

<script>
  export default {
    methods: {
        showDialog(flag) {
            this.$fmdialog({
                type: flag === 1 ? 'success' : 'failure',
                message: '我是对话框'
            });
        },

        showMaskDialog () {
            this.$fmdialog({
                type: 'success',
                message: '我是对话框, 并且带了炫酷的遮罩, 哈哈哈哈',
                mask: true
            });
        },

        showInteractionDialog(isSingle) {
            this.$fmdialog({
                message: '有交互行为的对话框',
                isSingle,
                onConfirm: () => alert('点击了确认')
            });
        }
    }
  }
</script>


